<template>
  <div class="bg-white rounded-xl shadow p-4 md:mr-4 mb-8">
    <h1 class="font-bold text-xl mb-2 leading-none">{{ array.averia }}</h1>
    <h1 class="font-bold text-xs mb-2 leading-none uppercase">
      {{ array.soluciones.length }} averas revisadas
    </h1>
    <PieChart :chartData="testData" />
  </div>
</template>

<script lang="ts">
import { defineComponent, ref } from "vue";
import { PieChart } from "vue-chart-3";

export default defineComponent({
  name: "Graficogiftpie",
  components: { PieChart },
  props: {
    array: {
      type: Object,
      required: true,
      value: [],
    },
  },

  setup(props) {
    const ids = ref([]);
    const labels = ref([]);
    const data = ref([]);

    props.array.soluciones.forEach((item) => {
      if (ids.value.indexOf(item.idSolucion) >= 0) {
        data.value[ids.value.indexOf(item.idSolucion)] += 1;
        return;
      }

      if (ids.value.indexOf(item.idSolucion) < 0) {
        ids.value.push(item.idSolucion);
        labels.value.push(item.solucion);
        data.value.push(1);
        return;
      }
    });

    const testData = {
      labels: labels.value, //props.etiquetas
      datasets: [
        {
          label: "# Averas revisadas",
          data: data.value, //props.datos
          backgroundColor: [
            "#ffadad",
            "#ffd6a5",
            "#fdffb6",
            "#caffbf",
            "#9bf6ff",
            "#a0c4ff",
            "#bdb2ff",
            "#ffc6ff",
          ],
        },
      ],
    };

    return { testData };
  },
});
</script>

<style scoped></style>
